body {
  line-height: 22px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  padding: 0;
  font-size: 14px;
  color: #414141;
  background-color: rgb(241, 243, 244);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

svg {
  cursor: pointer;
  margin: 0 5px;
  font-size: 1.2rem;
}

.outer-container {
  height: 100%;
}

.outer-container > header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  top: 0;
  height: 80px;
  line-height: 80px;
  padding: 0 270px;
  font-size: 14px;
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.05), 0 1px 0 rgba(0, 0, 0, 0.05);
  background: #fff;
  z-index: 1;
}

.outer-container > header #profile-icon {
  right: 279px;
}

.outer-container > header .pricing-list ul {
  display: flex;
  align-items: center;
  list-style: none;
}
.outer-container > header .pricing-list ul li {
  margin: 10px;
}
.outer-container > header .pricing-list ul .active {
  border-bottom: 5px solid #5d8fe9;
}
.outer-container > main {
  background: #f0f3f4;
  padding: 112px 150px 1px 150px;
  display: flex;
  width: 100%;
  justify-content: center;
  position: relative;
}

.outer-container > header .tab-name > img {
  object-fit: contain;
  height: 50px;
}

#profile-icon {
  min-width: 130px;
  float: right;
  cursor: pointer;
  padding: 0 20px;
  position: absolute;
  right: 0px;
  top: 0;
}
#profile-icon > i {
  font-size: 70%;
  margin-left: 8px;
}
#profile-icon .menu {
  display: none;
  position: absolute;
  background: #fff;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
  top: 100%;
  right: 0;
  width: 100%;
  min-width: 120px;
  padding: 10px 12px;
}

#profile-icon .menu i {
  font-size: 80%;
  color: #555;
  position: relative;
  top: -1px;
  left: -2px;
  margin-right: 6px;
}

#profile-icon a,
#profile-icon .logout-btn {
  display: block;
  line-height: 14px;
  transition: all 0.35s ease-in-out;
  border: 2px solid rgb(229 125 121);
  color: rgb(229 125 121);
  background-color: rgb(247 227 226);
  font-weight: bold;
  display: flex;
  align-items: center;
  padding: 7px 9px;
}

#profile-icon a:hover,
#profile-icon .logout-btn:hover {
  border: 2px solid rgb(229 125 121);
  color: white;
  background-color: rgb(229 125 121);
  font-weight: bold;
  border-radius: 0px !important;
}

#profile-icon:hover {
  background: #eee;
}

#profile-icon:hover .menu {
  display: block;
}

.wrapper {
  margin-right: 279px;
  margin-left: 270px;
}

.box {
  background-color: white;
  box-shadow: 0 3px 10px rgb(0 0 0 / 0.2);
  margin-top: 20px;
  margin-bottom: 50px;
  padding: 10px 20px;
}

.outer-container .box {
  width: 100vw;
  height: 100%;
  box-shadow: none;
}

.grey-strip {
  background-color: rgb(241, 243, 244);
}

.cards-container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding: 20px;
  background: #edf0f5;
}

.card-box {
  width: 330px;
  height: 162px;
  margin: 12px;
  border-radius: 9px;
  font-size: 16px;
  position: relative;
  cursor: pointer;
  box-sizing: border-box;
  display: inline-block;
  overflow: hidden;
  padding: 20px;
  position: relative;
}

.card-box::after {
  content: '';
  width: 120px;
  height: 120px;
  border-radius: 50%;
  background: #fff6f6;
  position: absolute;
  right: -20px;
  top: -60px;
  z-index: 0;
  opacity: 0.1;
}

.card-box::before {
  content: '';
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background: #fff6f6;
  position: absolute;
  left: -30px;
  bottom: -30px;
  z-index: 0;
  opacity: 0.1;
}

.card-theme-1 {
  background-color: rgb(16, 54, 108);
  color: white;
}

.card-theme-2 {
  background-color: rgb(36, 68, 134);
  color: white;
}

.card-theme-3 {
  background-color: rgb(39, 45, 107);
  color: white;
}

.last-card {
  background-color: rgb(86, 101, 128);
  width: 120px;
  height: 162px;
  margin: 12px;
  border-radius: 9px;
  font-size: 16px;
  position: relative;
  cursor: pointer;
  box-sizing: border-box;
  display: grid;
  place-items: center;
  overflow: hidden;
  padding: 20px;
}

.last-card > svg {
  color: white;
  font-weight: bold;
  font-size: 2.3rem;
}

.card-logo {
  right: 20px;
  position: absolute;
  z-index: 6;
}

.card-logo > img {
  min-width: 30px;
}

.card-details {
  bottom: 30px;
  position: absolute;
}

.bank-name {
  margin-bottom: 10px;
  font-weight: 600;
}

.card-number {
  display: flex;
  align-items: center;
  font-weight: 600;
  font-size: 1.3rem;
}

.card-number > svg {
  margin: 0 2.5px;
  font-size: 8px;
}

.card-number > svg:last-child {
  margin-right: 12px;
}

.detail-row {
  display: flex;
  flex-direction: column;
  margin-top: 15px;
  margin-left: 12px;
}

.detail-header {
  color: gray;
  font-weight: 600;
  cursor: pointer;
  padding: 10px 5px;
}

.detail-value {
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: initial;
  margin-top: 12px;
  margin-bottom: 12px;
}

.customer-btn {
  display: inline-block;
  border-radius: 3px;
  padding: 8px 12px !important;
  font-size: 14px;
  font-family: inherit;
  border: 1px solid #dbdbdb;
  background: #fff;
  vertical-align: middle;
  transition: 0.2s;
}

.btn {
  display: inline-block;
  border-radius: 3px;
  padding: 4px 12px !important;
  font-size: 14px;
  font-family: inherit;
  border: 1px solid #dbdbdb;
  background: #fff;
  vertical-align: middle;
  transition: 0.2s;
  margin: 10px;
}

.btn-hide-filters {
  border: 1px solid dodgerblue;
  color: dodgerblue;
  background-color: rgb(233, 241, 252);
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
}

.equal-width {
  width: 49%;
}

.fill {
  border: 1px solid dodgerblue;
  color: #fff;
  background-color: dodgerblue;
}

.tabs-box .tabs-control .pill {
  cursor: pointer;
}

.row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.column {
  display: flex;
  flex-direction: column;
}

.pill {
  padding: 2px 12px;
  font-size: 11px;
  border-radius: 4px;
  color: #fff;
  text-align: center;
  white-space: nowrap;
  margin: 1px;
  display: inline-block;
  border-radius: 16px;
  font-weight: bold;
  vertical-align: middle;
}

.danger {
  background-color: #f05150;
  color: white;
}

.success {
  background-color: #2ecc71;
  color: white;
}

.muted {
  color: #58666e;
  background-color: #d8e1e3;
  margin-right: 5px;
  margin-bottom: 5px;
}

.label-danger {
  background-color: rgb(247 227 226);
  color: rgb(229 125 121);
}

.label-success {
  background-color: rgba(31, 137, 14, 0.1);
  color: rgb(94 155 71);
}

.label-muted {
  color: #58666e;
  background-color: #d8e1e3;
  margin-right: 5px;
  margin-bottom: 5px;
}

.label-paused {
  background-color: rgb(251 233 203);
  color: rgb(201 154 90);
}

.label-expired {
  background-color: rgb(215 220 229);
  color: rgb(110 125 151);
}

.btn-default {
  background-color: #fff;
  border: 1px solid #dee5e7;
  color: #000;
}

.btn-default:hover {
  background-color: #f6f6f6;
}

.btn-default:active {
  background-color: #f0f0f0;
}

.chakra-modal__footer > .occupy-width {
  width: 100%;
}

.chakra-modal__footer {
  display: flex;
  flex-direction: column;
}

.chakra-modal__footer > button {
  margin-top: 20px;
}

.occupy-width {
  width: 90%;
}

.chakra-modal__content {
  width: max-content !important;
}

.chakra-modal__close-btn > svg {
  font-size: 0.8rem;
  color: gray;
}

.top-bar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 24px;
}

.customer-right {
  flex: 0.35;
  padding: 24px;
  display: flex;
  justify-content: center;
}

.customer-left {
  flex: 0.65;
  margin-right: 20px;
}

.row-start {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.row-end {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.bold {
  font-weight: bold;
  font-size: 1.3rem;
}

.semi-bold {
  font-weight: semi-bold;
  font-size: 1rem;
}

.customer-right > .detail-header {
  margin-bottom: 29px;
}

.heimdall {
  display: flex;
}

.table-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  transition: all 0.3s ease-in-out;
}

.customer-left > .detail-header {
  margin-top: 30px;
  margin-bottom: 11px;
}

.table-row > .table-data:first-child {
  font-weight: semi-bold;
}

.table-data {
  display: flex;
  align-items: center;
  justify-content: initial;
  flex: 1;
}

.green-tick {
  background-color: rgb(211 241 201);
  color: rgb(94 155 71);
  border-radius: 100%;
  border: 2px solid rgb(94 155 71);
  padding: 5px;
  font-size: 2.1rem;
}

.red-error {
  background-color: rgb(247 227 226);
  color: rgb(229 125 121);
  border-radius: 100%;
  border: 2px solid rgb(229 125 121);
  padding: 5px;
  font-size: 2.1rem;
}

.customer-left .table-row:nth-child(2) {
  border-radius: 7px 7px 0 0;
}

.customer-left .table-row:last-child {
  border-radius: 0 0 7px 7px;
}

.customer-left .table-row:not(:first-child) {
  border: 2px solid lightgrey;
  padding: 10px;
}

.customer-left .table-row:not(:first-child):hover {
  border: 2px solid black;
  cursor: pointer;
}

.table-data > img {
  object-fit: contain;
  min-width: 35px;
}

.customer-left .table-row .table-data:nth-child(3) {
  max-width: max-content;
  padding-right: 10px;
}

.info-block {
  min-height: 18px;
  line-height: 18px;
  margin-top: 4px;
  word-break: break-word;
  font-size: 12px;
  color: #58666e;
  text-transform: capitalize;
}

.detail-container {
  border: 2px solid black;
  border-radius: 7px;
  padding: 20px;
  width: 400px;
  /* height: 550px; */
  position: relative;
}

.detail-container .detail-data .table-row {
  margin: 10px 0;
  justify-content: initial;
}

.detail-container .detail-data .table-row:first-child {
  margin-top: 45px;
}

.detail-container .detail-data .table-row .table-data {
  flex: initial;
}

.detail-container .detail-data .table-row .table-data:first-child {
  color: gray;
  margin-right: 25px;
}

.detail-container .detail-data .table-row .table-data:nth-child(2) {
  font-weight: bold;
}

.detail-container > button {
  width: 100%;
  margin-top: 20px;
}

.active-token {
  border: 2px solid black !important;
  cursor: pointer;
}

.token-info {
  margin-top: 45px;
  min-width: 300px;
}

.token-info .table-row {
  margin: 12px 0;
}

.token-info .table-row .table-data:first-child {
  font-weight: bold;
}

.token-info .table-row .table-data:nth-child(2) {
  color: darkslategrey;
}

.detail-data .customer-btn {
  margin: 0px;
}

.wrapper-prashant {
  margin-left: 20px;
  margin-right: 20px;
}

.wrapper-prashant > div {
  flex: 1;
  padding: 10px;
}
.table-row-flexed > div:nth-child(1) {
  flex: calc(100% - 200px);
}
.table-row-flexed > div:nth-child(2) {
  flex: 80px;
}
.table-row-flexed > div:nth-child(3) {
  flex: 120px;
}

.justify-center {
  justify-content: center;
  text-align: center;
}

.table-row.bordered {
  padding: 10px 5px;
  border-bottom: 1px solid #e7ebf1;
  cursor: pointer;
}

.table-bordered {
  border: 1px solid #e7ebf1;
}

.boogey {
  padding: 0 30px;
}
